

/* Headings and labels */
h4 {
  font-size: 24px; 
  font-weight: 600; 
  color: #000; 
  opacity: 0.85; 
}

label {
  font-size: 12.5px; 
  color: #000;
  opacity: 0.8;
  font-weight: 400; 
}

/* Form styles */
form {
  padding: 40px 30px; 
  background: #fefefe; 
  display: flex; 
  flex-direction: column;
  align-items: flex-start; 
  padding-bottom: 20px; 
  width: 300px;
}

form h4 {
  margin-bottom: 20px;
  color: rgba(#000, 0.5);
}

form h4 span {
  color: rgba(#000, 1);
  font-weight: 700; 
}

form p {
  line-height: 155%; 
  margin-bottom: 5px; 
  font-size: 14px; 
  color: #000; 
  opacity: 0.65;
  font-weight: 400; 
  max-width: 200px; 
  margin-bottom: 40px; 
}

/* Links */
a.discrete {
  color: rgba(#000, 0.4); 
  font-size: 14px; 
  border-bottom: solid 1px rgba(#000, 0);
  padding-bottom: 4px;  
  margin-left: auto; 
  font-weight: 300; 
  transition: all 0.3s ease; 
  margin-top: 40px; 
}

a.discrete:hover {
  border-bottom: solid 1px rgba(#000, 0.2);
}

/* Buttons */
button {
  -webkit-appearance: none; 
  width: auto;
  min-width: 100px;
  border-radius: 24px; 
  text-align: center; 
  padding: 15px 40px;
  margin-top: 5px; 
  background-color: #6e231f; /* Use the variable value directly */
  color: #fff; 
  font-size: 14px;
  margin-left: auto; 
  font-weight: 500; 
  box-shadow: 0px 2px 6px -1px rgba(0,0,0,0.13); 
  border: none;
  transition: all 0.3s ease; 
  outline: 0; 
}

button:hover {
  transform: translateY(-3px);
  box-shadow: 0 2px 6px -1px #6e231f; /* Use the variable value directly */
}

button:active {
  transform: scale(0.99);
}

/* Inputs */
input {
  font-size: 16px; 
  padding: 20px 0px; 
  height: 56px; 
  border: none; 
  border-bottom: solid 1px rgba(0,0,0,0.1); 
  background: #fff; 
  width: 280px; 
  box-sizing: border-box; 
  transition: all 0.3s linear; 
  color: #000; 
  font-weight: 400;
  -webkit-appearance: none; 
}

input:focus {
  border-bottom: solid 1px #6e231f; /* Use the variable value directly */
  outline: 0; 
  box-shadow: 0 2px 6px -8px #6e231f; /* Use the variable value directly */
}

/* Floating labels */
.floating-label {
  position: relative; 
  margin-bottom: 10px;
  width: 100%; 
}

.floating-label label {
  position: absolute; 
  top: calc(50% - 7px);
  left: 0; 
  opacity: 0; 
  transition: all 0.3s ease; 
  padding-left: 44px; 
}

.floating-label input {
  width: calc(100% - 44px); 
  margin-left: auto;
  display: flex; 
}

.floating-label .icon {
  position: absolute; 
  top: 0; 
  left: 0; 
  height: 56px; 
  width: 44px; 
  display: flex; 
}

.floating-label .icon svg {
  height: 30px; 
  width: 30px; 
  margin: auto;
  opacity: 0.15; 
  transition: all 0.3s ease; 
}

.floating-label input:not(:placeholder-shown) {
  padding: 28px 0px 12px 0px; 
}

.floating-label input:not(:placeholder-shown) + label {
  transform: translateY(-10px); 
  opacity: 0.7; 
}

.floating-label input:valid:not(:placeholder-shown) + label + .icon svg {
  opacity: 1; 
  path {
    fill: #6e231f; /* Use the variable value directly */
  }      
}

.floating-label input:not(:valid):not(:focus) + label + .icon {
  animation-name: shake-shake;
  animation-duration: 0.3s;
}

/* Keyframes for shake animation */
@keyframes shake-shake {
  0% { transform: translateX(-3px); }
  20% { transform: translateX(3px); }
  40% { transform: translateX(-3px); }
  60% { transform: translateX(3px); }  
  80% { transform: translateX(-3px); }
  100% { transform: translateX(0px); }
}

/* Session container */
.session {
  display: flex; 
  flex-direction: row; 
  width: auto; 
  height: auto; 
  margin: auto; 
  background: #ffffff; 
  border-radius: 4px; 
  box-shadow: 0px 2px 6px -1px rgba(0,0,0,0.12);
}

/* Left side background and SVG styling */
.left {
  width: 300px; 
  height: auto; 
  min-height: 100%; 
  position: relative; 
  background-image: url("../assets/images/login_photo.JPG");
  background-size: cover;
  border-top-left-radius: 4px; 
  border-bottom-left-radius: 4px; 
}

.left svg {
  height: 40px; 
  width: auto; 
  margin: 20px; 
}
